
"use client"

import Link from "next/link"
import { usePathname } from "next/navigation"
import { cn } from "@/lib/utils"
import { useAuth } from "@/context/auth-context"
import { Role } from "@/lib/types"

export function DashboardNav() {
  const pathname = usePathname()
  const { user } = useAuth()

  if (!user) return null

  const navItems = [
    { href: "/dashboard", label: "My Schedule", roles: ["admin", "teacher", "upper-management", "high-level-dashboard"] },
    { href: "/dashboard/requests", label: "Requests", roles: ["admin", "upper-management", "high-level-dashboard"] },
    { href: "/dashboard/students", label: "Students", roles: ["admin", "upper-management", "high-level-dashboard"] },
    { href: "/dashboard/users", label: "Users", roles: ["admin", "high-level-dashboard"] },
    { href: "/dashboard/semesters", label: "Semesters", roles: ["admin", "high-level-dashboard"] },
    { href: "/dashboard/payments", label: "Payments", roles: ["admin", "upper-management", "high-level-dashboard"] },
    { href: "/dashboard/reports", label: "Reports", roles: ["admin", "upper-management", "high-level-dashboard"] },
    { href: "/dashboard/settings", label: "Settings", roles: ["admin", "high-level-dashboard"] },
  ]

  const placeholderPages = [
    "/dashboard/users",
    "/dashboard/semesters",
    "/dashboard/payments",
    "/dashboard/reports",
    "/dashboard/settings",
  ];


  const userHasRole = (roles: Role[]) => roles.includes(user.activeRole)
  const isAdmin = user.activeRole === 'admin';

  return (
    <nav className="hidden md:flex items-center gap-6 text-sm">
      {navItems.map((item) =>
        userHasRole(item.roles) && (
          <Link 
            key={item.href} 
            href={(placeholderPages.includes(item.href) && !isAdmin) ? "#" : item.href}
            className={cn(
              "transition-colors hover:text-foreground/80",
              pathname === item.href ? "text-foreground" : "text-foreground/60",
              (placeholderPages.includes(item.href) && !isAdmin) && "cursor-not-allowed opacity-50"
            )}
          >
            {item.label}
          </Link>
        )
      )}
    </nav>
  )
}
